import { IRng } from "db://assets/scripts/game/domain/Contract";
import { BlastGroupConfig as BlastGroupConfig } from "db://assets/scripts/shared/castledb/storage/sheets/Blast";
import { IBlastGroupSetupConfig, ILevelConfig } from "db://assets/scripts/shared/castledb/storage/sheets/Levels";
import { Random, WeigthedRandomizer } from "db://assets/scripts/shared/utils/Random";


export class RandomService implements IRng {

    private colorGroupRandomizer: WeigthedRandomizer<BlastGroupConfig>;
    private random: Random;

    constructor(levelConfig: ILevelConfig) {

        this.random = new Random(levelConfig.randomSeed);

        const colorGroupProbabilities = this.toColorGroupProbabilities(levelConfig.blast.colorGroupSetup);

        this.colorGroupRandomizer = new WeigthedRandomizer<BlastGroupConfig>(
            this.random,
            colorGroupProbabilities
        );
    }

    public next(): BlastGroupConfig {
        return this.colorGroupRandomizer.next();
    }

    private *toColorGroupProbabilities(
        chipConfigs: IBlastGroupSetupConfig[]
    ): Generator<[BlastGroupConfig, number]> {

        for (const chipConfig of chipConfigs) {

            const colorGroup = chipConfig.group;

            yield [colorGroup, chipConfig.probability];
        }
    }
}