import { Piece } from "db://assets/scripts/game/domain/blast/Piece";
import { IIdFactory, IPiece } from "db://assets/scripts/game/domain/Contract";
import { BlastGroupConfig } from "db://assets/scripts/shared/castledb/storage/sheets/Blast";


export class PieceFactory {

    private readonly idFactory: IIdFactory;

    constructor(idFactory: IIdFactory) {

        this.idFactory = idFactory;
    }

    public create(config: BlastGroupConfig): IPiece {

        return new Piece(this.idFactory.newId(), config);
    }
}