import { Grid } from "db://assets/scripts/game/domain/Grid";
import { RandomService } from "db://assets/scripts/game/domain/service/RandomService";
import { PieceFactory } from "db://assets/scripts/game/domain/service/PieceFactory";
import { ILevelConfig } from "db://assets/scripts/shared/castledb/storage/sheets/Levels";
import { ICell, IGrid, IGridFactory, IRng } from "db://assets/scripts/game/domain/Contract";
import { Cell } from "db://assets/scripts/game/domain/blast/Cell";

export class GridFactory implements IGridFactory {

	private readonly pieceFactory: PieceFactory;

	constructor(tileFactory: PieceFactory) {

		this.pieceFactory = tileFactory;
	}

	public create(config: ILevelConfig, random: IRng): IGrid {

		const height = config.blast.gridSize.y;
		const width = config.blast.gridSize.x;
		const cells: ICell[] = [];

		for (let y = 0; y < height; y++) {

			for (let x = 0; x < width; x++) {

				const tileConfig = random.next();
				const piece = this.pieceFactory.create(tileConfig);
				cells[y * width + x] = new Cell(x, y, piece);
			}
		}

		return new Grid(width, height, cells);
	}
}