import { _decorator, Component, Vec2 } from 'cc';
import { IViewPort } from 'db://assets/scripts/game/domain/Contract';
import { Game } from 'db://assets/scripts/game/domain/Game';
import { BlastBoardView } from 'db://assets/scripts/game/view/blast/BlastBoardView';
import { StatsView } from 'db://assets/scripts/game/view/stats/StatsView';
import { ViewFacade } from 'db://assets/scripts/game/view/ViewFacade';
import { CdbAdapter } from 'db://assets/scripts/shared/castledb/CdbAdapter';
import { CdbData } from 'db://assets/scripts/shared/castledb/CdbData';

const { ccclass, property } = _decorator;

@ccclass("Bootstrap")
export class Bootstrap extends Component {

	@property(BlastBoardView)
	private boardView: BlastBoardView;

	@property(StatsView)
	private hud: StatsView;

	public game: Game;
	public ready: Promise<Game>;

	private cdbData: CdbData;

	protected onLoad(): void {

		this.ready = this.init();
	}

	private async init(): Promise<Game> {

		this.cdbData = await new CdbAdapter().init();
		const viewFacade = new ViewFacade(this.boardView, this.hud);

		this.game = new Game(
			this.cdbData.gameLevels.getById("one"),
			viewFacade
		);

		return this.game;
	}
}