import { log } from "cc";
import { ICell, IGameState } from "db://assets/scripts/game/domain/Contract";
import { GameContext } from "db://assets/scripts/game/domain/GameContext";
import { ReadyState } from "db://assets/scripts/game/domain/gameState/ReadyState";

export class TilesFallingState implements IGameState {

	constructor(
		private ctx: GameContext
	) { }

	async enter(): Promise<void> {

		const moves = this.ctx.gravity.applyGravity(this.ctx.grid);
		const gravityBeforePromise = this.ctx.view.dropGravity(moves);

		const spawnMoves = this.ctx.refill.refill(this.ctx.grid, this.ctx.rng, this.ctx.idFactory);

		log(moves);
		log(spawnMoves);

		await this.ctx.view.refill(spawnMoves, this.ctx.grid.cells);
		await gravityBeforePromise;

		this.ctx.setState(new ReadyState(this.ctx));
	}

	handleTap(cell: ICell): Promise<void> | void { }
}