
export class Random {

    private readonly A: number = 1664525;
    private readonly C: number = 1013904223;
    private readonly M: number = 4294967296;

    private seed: number;

    public constructor(seed: number) {

        this.seed = seed;
    }

    public next(): number {

        this.seed = (this.A * this.seed + this.C) % this.M;

        return this.seed / this.M;
    }

    public nextRange(min: number, max: number): number {

        return this.next() * (max - min) + min;
    }

    public nextRangeInt(min: number, max: number): number {

        return Math.floor(this.next() * (max - min)) + min;
    }
}

export class WeigthedRandomizer<T> {

    public readonly items: readonly T[];

    private readonly random: Random;
    private readonly cumulativeWeights: readonly number[];

    constructor(random: Random, generator: Generator<[T, number]>) {

        const items: T[] = [];
        const cumulativeWeights: number[] = [];

        let cumulativeWeight: number = 0;

        for (const [item, weight] of generator) {

            cumulativeWeight += weight;

            items.push(item);
            cumulativeWeights.push(cumulativeWeight);
        }

        this.random = random;
        this.items = items;
        this.cumulativeWeights = cumulativeWeights;
    }

    public next(): T {

        const maxWeight = this.cumulativeWeights[this.cumulativeWeights.length - 1];
        const randomWeight = this.random.nextRangeInt(0, maxWeight);

        for (let i = 0; i < this.cumulativeWeights.length; i++) {

            if (randomWeight < this.cumulativeWeights[i])
                return this.items[i];
        }

        return this.items[this.items.length - 1];
    }
}