import * as Data from "db://assets/scripts/shared/castledb/main.js";
import { CdbData } from "db://assets/scripts/shared/castledb/CdbData";

export class CdbAdapter {

    public async init(): Promise<CdbData> {

        try {
            const rawData = await Data.default.run();
            const data = new CdbData(rawData);

            return data;

        } catch (e) {

            throw "an error occured while parsing cdb: " + e;
        }
    }
}