import { ICell, IGameState } from "db://assets/scripts/game/domain/Contract";
import { GameContext } from "db://assets/scripts/game/domain/GameContext";
import { DefeatState } from "db://assets/scripts/game/domain/gameState/DefeatState";
import { TilesFallingState } from "db://assets/scripts/game/domain/gameState/TilesFallingState";
import { VictoryState } from "db://assets/scripts/game/domain/gameState/VictoryState";
import { FloodFillGroupFinder } from "db://assets/scripts/game/domain/service/FloodFillGroupFinder";

export class ReadyState implements IGameState {

	private static PTS_PER_TILE = 10;

	constructor(
		private ctx: GameContext,
		private clusters = new FloodFillGroupFinder()
	) { }

	enter() {

		this.updateStats();

		this.checkGameCompletionCondition();
	}

	handleTap(cell: ICell) {

		const cluster = this.clusters.find(this.ctx.grid, cell);

		if (cluster.length < 2) return;

		cluster.forEach(c => c.piece = null);

		this.ctx.progress.addScore(cluster.length * ReadyState.PTS_PER_TILE);
		this.ctx.progress.consumeTurn();

		this.ctx.view.clear(cluster).then(() =>
			this.ctx.setState(new TilesFallingState(this.ctx))
		);

		this.updateStats();
	}

	private updateStats() {

		const view = this.ctx.view;
		const progress = this.ctx.progress;

		if (view.updateTurns)
			view.updateTurns(progress.movesLeft);
		if (view.updateScore)
			view.updateScore(progress.score, progress.targetScore);
	}

	private checkGameCompletionCondition() {

		if (this.ctx.progress.hasWon) {
			this.ctx.setState(new VictoryState(this.ctx));
			return;
		}
		if (this.ctx.progress.outOfMoves) {
			this.ctx.setState(new DefeatState(this.ctx));
			return;
		}
	}
}