import { ICell, IGrid, IGroupFindService } from "db://assets/scripts/game/domain/Contract";


export class FloodFillGroupFinder implements IGroupFindService {

	private dirs = [[1, 0], [-1, 0], [0, 1], [0, -1]] as const;

	find(grid: IGrid, start: ICell): ICell[] {

		const target = start.piece?.type;

		if (target === undefined) return [];

		const visited = new Set<ICell>([start]);
		const queue: ICell[] = [start];

		while (queue.length) {

			const cell = queue.pop();

			for (const [dx, dy] of this.dirs) {

				const nx = cell.x + dx, ny = cell.y + dy;

				if (nx < 0 || nx >= grid.width || ny < 0 || ny >= grid.height)
					continue;

				const n = grid.getCell(nx, ny);

				if (visited.has(n))
					continue;

				if (n.piece && n.piece.type === target) {
					visited.add(n);
					queue.push(n);
				}
			}
		}
		return Array.from(visited);
	}
}