import { SheetsClassMap } from "db://assets/scripts/shared/castledb/storage/Typings";

type RawColumnType = {
    _hx_name?: string;
    _hx_index: number;
    __enum__: string;
    sheet?: string;
};

type RawColumnDefinition = {
    name: string;
    type: RawColumnType;
};

export type RawSheetData<T> = {
    name: string;
    all: T[];
    sheet: {
        name: string;
        columns: RawColumnDefinition[];
        lines: T[];
        separators: any[];
        props: Record<string, any>;
    };
    byId: { h: Record<string, T> };
    byIndex: T[];
};

export class StringValueWrapper<T = any> {
    constructor(private value: string) { }

    toString() { return this.value; }
    parseJson<U extends T>(): U { return JSON.parse(this.value); }
    parseNumber(): number { return Number(this.value); }
}


export class BaseCdbSheet<T extends object> {

    protected dataById: Map<string, T>;
    protected dataByIndex: T[];
    protected columns: RawColumnDefinition[];
    protected sheets: Partial<SheetsClassMap>;
    protected sheetName: string;

    constructor(raw: RawSheetData<T>, resolver: Partial<SheetsClassMap>) {

        this.dataById = new Map(Object.entries(raw.byId.h));
        this.dataByIndex = raw.byIndex;
        this.columns = raw.sheet.columns;
        this.sheets = resolver;
        this.sheetName = raw.name;
    }

    protected getColumnType(columnName: string): RawColumnType | undefined {
        return this.columns.find(col => col.name === columnName)?.type;
    }

    public getById(id: string): T | undefined {

        const entry = this.dataById.get(id);

        if (!entry)
            return undefined;

        return this.proxyWrapper(entry, this.columns, this.sheetName) as T;
    }

    public getByIndex(index: number): T | undefined {
        return this.proxyWrapper(this.dataByIndex[index], this.columns, this.sheetName) as T;
    }

    public getAll(): T[] {
        return this.proxyWrapper(this.dataByIndex, this.columns, this.sheetName) as T[];
    }

    //     sheets  resolver
    private proxyWrapper(obj: any, columns: RawColumnDefinition[], sheetName: string): any {

        if (Array.isArray(obj)) {
            return obj.map(item => this.proxyWrapper(item, columns, sheetName));
        }

        if (obj && typeof obj === "object") {

            if (!obj.__proxy__) {

                obj.__proxy__ = new Proxy(obj, {

                    get: (target, prop) => {

                        //      
                        if (prop === "__proxy__" || typeof prop === "symbol") {

                            return target[prop];
                        }

                        //     
                        if (target[prop] && target[prop].__proxy__) {

                            return target[prop].__proxy__;
                        }

                        if (typeof prop === "string" && target[prop] !== undefined) {

                            //    (`rules` sheet) /   JSON 
                            if (prop === "value" && typeof target[prop] === "string") {

                                return new StringValueWrapper(target[prop]);
                            }

                            const columnDef = columns.find(col => col.name === prop);
                            const columnType = columnDef?.type;

                            //     
                            if (columnType?.sheet && typeof target[prop] === "string") {

                                const resolved = this.sheets[columnType.sheet].getById(target[prop]);
                                const otherSheet = this.sheets?.[columnType.sheet];
                                const otherColumns = otherSheet?.columns ?? [];
                                return this.proxyWrapper(resolved, otherColumns, columnType.sheet);
                            }

                            //   props/list
                            if (typeof target[prop] === "object" && target[prop] !== null) {

                                //   
                                const nestedSheetName = sheetName + "@" + prop;
                                const nestedSheet = this.sheets?.[nestedSheetName];
                                const nestedColumns = nestedSheet?.columns ?? Object.entries(target[prop])
                                    .map(([k, v]) => ({

                                        name: k,
                                        type: { _hx_name: undefined, _hx_index: -1, __enum__: "cdb.ColumnType" }
                                    }));

                                return this.proxyWrapper(target[prop], nestedColumns, nestedSheetName);
                            }
                        }
                        return target[prop];
                    }
                });
            }

            return obj.__proxy__;
        }
        return obj;
    }
}