
export class GameProgress {

	public get hasWon() {
		return this.score >= this.targetScore;
	}

	public get outOfMoves() {
		return this.movesLeft <= 0;
	}

	constructor(
		public score = 0,
		public movesLeft = 0,
		public readonly targetScore = 0
	) { }

	public addScore(delta: number) {

		this.score += delta; return this.score;
	}

	public consumeTurn() {

		if (this.movesLeft > 0)
			this.movesLeft--;

		return this.movesLeft;
	}
}