import { ICell, IGrid, IPiece } from "db://assets/scripts/game/domain/Contract";

export class Grid implements IGrid {

    public readonly width: number;
    public readonly height: number;
    public readonly cells: ICell[];

    constructor(width: number, height: number, cells: ICell[]) {

        this.width = width;
        this.height = height;
        this.cells = cells;
    }

    public getCell(x: number, y: number): ICell {
        return this.cells[y * this.width + x];
    }
}