import { _decorator, Component, instantiate, log, Node, Prefab, tween, UITransform, Vec2, Vec3 } from 'cc';
import { ICell, IViewPort, IPiece } from 'db://assets/scripts/game/domain/Contract';
import { DropMove } from 'db://assets/scripts/game/domain/service/GravityResolver';
import { SpawnMove } from 'db://assets/scripts/game/domain/service/RefillService';
import { BlastGroupKeyId } from 'db://assets/scripts/shared/castledb/storage/sheets/Blast';

const { ccclass, property, requireComponent } = _decorator;

type CellNode = { node: Node; piece?: IPiece | null };

@ccclass("BlastBoardView")
export class BlastBoardView extends Component {

	private static readonly FALL_DURATION_OFFSET = 0.15;
	private static readonly FALL_DURATION_MULT = 0.05;

	@property({ visible: true })
	public readonly cellSize: Vec2 = new Vec2(200, 200);

	@property({ visible: true })
	public readonly cellSpacing: Vec2 = new Vec2(10, 10);

	@property(Node)
	public boardRoot: Node;

	@property(Prefab)
	private redPiece: Prefab;

	@property(Prefab)
	private pinkPiece: Prefab;

	@property(Prefab)
	private bluePiece: Prefab;

	@property(Prefab)
	private greenPiece: Prefab;

	@property(Prefab)
	private yellowPiece: Prefab;

	private width = 0;
	private height = 0;
	private cells: CellNode[] = [];

	public async refill(
		moves: ReadonlyArray<SpawnMove>,
		cells: ReadonlyArray<ICell>
	): Promise<void> {

		for (const move of moves) {

			const x = move.fromX;
			const y = move.toY;

			const holder = new Node(`c_${x}_${y}`);
			holder.setPosition(this.cellToWorld(x, move.fromY));
			this.boardRoot.addChild(holder);
			this.cells[y * this.width + x] = { node: holder, piece: null };

			this.setPiece(x, y, cells[y * this.width + x].piece);

			const dest = this.cellToWorld(move.fromX, move.toY);
			this.dropNode(holder, move.fromY - move.toY, dest);
		}
	}

	public async initGrid(width: number, height: number, cells: ReadonlyArray<ICell>) {

		this.width = width; this.height = height;
		this.boardRoot.removeAllChildren();
		this.cells = new Array(width * height);

		for (let y = 0; y < height; y++) {

			for (let x = 0; x < width; x++) {

				const holder = new Node(`c_${x}_${y}`);
				holder.setPosition(this.cellToWorld(x, y));
				this.boardRoot.addChild(holder);
				this.cells[y * this.width + x] = { node: holder, piece: null };
			}
		}

		for (const cell of cells)
			this.setPiece(cell.x, cell.y, cell.piece);
	}

	public async clear(cells: ReadonlyArray<ICell>): Promise<void> {

		for (const cell of cells) {

			const id = cell.y * this.width + cell.x;
			this.cells[id].node?.removeFromParent();
			this.cells[id].node = null;
			this.cells[id].piece = null;
		}
	}

	public async dropGravity(moves: readonly DropMove[]) {

		const promises: Promise<void>[] = [];

		for (const move of moves) {

			const slot = this.cells[move.fromY * this.width + move.fromX];
			const node = slot.node;

			if (!node)
				continue;

			const dest = this.cellToWorld(move.fromX, move.toY);

			const promise = this.dropNode(node, move.fromY - move.toY, dest);
			promises.push(promise);

			this.cells[move.fromY * this.width + move.fromX].node = null;
			this.cells[move.toY * this.width + move.fromX].node = node;
		}

		await Promise.all(promises);
	}

	public worldToGridPosition(x: number, y: number): Vec2 {

		const local = this.boardRoot
			.getComponent(UITransform)
			.convertToNodeSpaceAR(new Vec3(x, y, 0));

		return this.localToGridPosition(new Vec2(local.x, local.y));
	}

	public isCoordinateValid(vec: Vec2): Boolean {

		return (vec.x >= 0 && vec.y >= 0 && vec.x < this.width && vec.y < this.height);
	}

	private dropNode(node: Node, dist: number, dest: Vec3) {

		return new Promise<void>(res => {
			tween(node)
				.to(
					BlastBoardView.FALL_DURATION_OFFSET + BlastBoardView.FALL_DURATION_MULT * dist,
					{ position: dest },
					{ easing: "quadIn" })
				.call((e) => res())
				.start();
		});
	}

	private setPiece(x: number, y: number, p: IPiece | null) {

		const slot = this.cells[y * this.width + x];

		if (!p)
			return;

		let prefab = this.prefabForType(p.type.id);
		const node = instantiate(prefab);

		slot.node.addChild(node);
		slot.piece = p;
	}

	private prefabForType(id: BlastGroupKeyId): Prefab {

		switch (id) {
			case BlastGroupKeyId.BLUE: return this.bluePiece;
			case BlastGroupKeyId.GREEN: return this.greenPiece;
			case BlastGroupKeyId.PINK: return this.pinkPiece;
			case BlastGroupKeyId.RED: return this.redPiece;
			case BlastGroupKeyId.YELLOW: return this.yellowPiece;
			default: return this.yellowPiece;
		}
	}

	private localToGridPosition(localPosition: Vec2): Vec2 {

		const cellSizeX = this.cellSize.x + this.cellSpacing.x;
		const cellSizeY = this.cellSize.y + this.cellSpacing.y;

		const localOffsetX = 0.5 * cellSizeX * this.width;
		const localOffsetY = 0.5 * cellSizeY * this.height;

		const originPositionX = localPosition.x + localOffsetX;
		const originPositionY = localPosition.y + localOffsetY;

		const gridPositionX = Math.floor(originPositionX / cellSizeX);
		const gridPositionY = Math.floor(originPositionY / cellSizeY);

		const gridPosition = new Vec2(gridPositionX, gridPositionY);

		return gridPosition;
	}

	private cellToWorld(x: number, y: number) {

		const cellSizeX = this.cellSize.x + this.cellSpacing.x;
		const cellSizeY = this.cellSize.y + this.cellSpacing.y;

		const originPositionX = cellSizeX * (0.5 + x);
		const originPositionY = cellSizeY * (0.5 + y);

		const localOffsetX = 0.5 * cellSizeX * this.width;
		const localOffsetY = 0.5 * cellSizeY * this.height;

		return new Vec3(originPositionX - localOffsetX, originPositionY - localOffsetY);
	}
}