import { _decorator, Component, EventMouse, EventTouch, input, Input, log, Node } from 'cc';
import { Bootstrap } from 'db://assets/scripts/game/Bootstrap';
import { Game } from 'db://assets/scripts/game/domain/Game';
import { BlastBoardView } from 'db://assets/scripts/game/view/blast/BlastBoardView';
import { game, view } from 'cc';

const { ccclass, property, requireComponent } = _decorator;

@ccclass("GameController")
@requireComponent(BlastBoardView)
export class GameController extends Component {

	@property(BlastBoardView)
	private gridView: BlastBoardView;

	@property(Bootstrap)
	private bootstrap: Bootstrap;

	private game: Awaited<Game>;

	async start() {

		await this.bootstrap.ready;
		this.game = this.bootstrap.game;

		const grid = this.game.grid;
		await this.gridView.initGrid(grid.width, grid.height, grid.cells);
	}

	protected onEnable(): void {

		input.on(Input.EventType.TOUCH_END, this.onTouchEnd, this);
		input.on(Input.EventType.MOUSE_UP, this.onTouchEnd, this);
	}

	onDestroy() {

		input.off(Input.EventType.TOUCH_END, this.onTouchEnd, this);
		input.off(Input.EventType.MOUSE_UP, this.onTouchEnd, this);
	}

	private onTouchEnd(e: EventTouch | EventMouse) {

		const p = e.getUILocation();
		const vec = this.gridView.worldToGridPosition(p.x, p.y);

		if (!this.gridView.isCoordinateValid(vec))
			return;

		this.game.tap(vec);
	}
}