import { ICell, IViewPort } from "db://assets/scripts/game/domain/Contract";
import { DropMove } from "db://assets/scripts/game/domain/service/GravityResolver";
import { SpawnMove } from "db://assets/scripts/game/domain/service/RefillService";
import { BlastBoardView } from "db://assets/scripts/game/view/blast/BlastBoardView";
import { StatsView } from "db://assets/scripts/game/view/stats/StatsView";

export class ViewFacade implements IViewPort {

	constructor(
		public readonly boardView: BlastBoardView,
		public readonly hud: StatsView
	) { }

	showDefeat(): Promise<void> {
		return this.hud.showDefeat();
	}

	showVictory(): Promise<void> {
		return this.hud.showVictory();
	}

	updateTurns(value: number): Promise<void> {
		this.hud.updateTurns(value);
		return Promise.resolve();
	}

	updateScore(value: number, target: number): Promise<void> {
		this.hud.updateScore(value, target);
		return Promise.resolve();
	}

	initGrid(width: number, height: number, cells: ReadonlyArray<ICell>): Promise<void> {
		return this.boardView.initGrid(width, height, cells);
	}

	clear(cells: ReadonlyArray<ICell>): Promise<void> {
		return this.boardView.clear(cells);
	}

	dropGravity(moves: ReadonlyArray<DropMove>): Promise<void> {
		return this.boardView.dropGravity(moves);
	}

	refill(moves: ReadonlyArray<SpawnMove>, cells: ReadonlyArray<ICell>): Promise<void> {
		return this.boardView.refill(moves, cells);
	}
}