import { Vec2 } from "cc";
import {
	IViewPort,
	IGameState,
	IGravityResolver,
	IGrid,
	IGridFactory,
	IRefillService,
	IRng
} from "db://assets/scripts/game/domain/Contract";
import { GameContext } from "db://assets/scripts/game/domain/GameContext";
import { ReadyState } from "db://assets/scripts/game/domain/gameState/ReadyState";
import { GameProgress } from "db://assets/scripts/game/domain/service/GameProgress";
import { GravityResolver } from "db://assets/scripts/game/domain/service/GravityResolver";
import { GridFactory } from "db://assets/scripts/game/domain/service/GridFactory";
import { SimpleIdFactory } from "db://assets/scripts/game/domain/service/IdService";
import { PieceFactory } from "db://assets/scripts/game/domain/service/PieceFactory";
import { RandomService } from "db://assets/scripts/game/domain/service/RandomService";
import { RefillService } from "db://assets/scripts/game/domain/service/RefillService";
import { ILevelConfig } from "db://assets/scripts/shared/castledb/storage/sheets/Levels";

export class Game {

	public readonly grid: IGrid;

	private ctx: GameContext;
	private state: IGameState;

	constructor(levelConfig: ILevelConfig, view: IViewPort) {

		const idFactory = new SimpleIdFactory();
		const pieceFactory = new PieceFactory(idFactory);
		const gridFactory = new GridFactory(pieceFactory);
		const gravity = new GravityResolver();
		const randomService = new RandomService(levelConfig);
		const refill = new RefillService();

		this.grid = gridFactory.create(levelConfig, randomService);

		this.ctx = new GameContext(
			levelConfig,
			this.grid,
			view,
			randomService,
			gravity,
			refill,
			idFactory,
			new GameProgress(0, levelConfig.rules.maxMoves, levelConfig.rules.targetScore),
			this.setState.bind(this)
		);

		this.setState(new ReadyState(this.ctx));
	}

	public tap(tilePos: Vec2) {

		const cell = this.ctx.grid.getCell(tilePos.x, tilePos.y);
		this.state.handleTap?.(cell);
	}

	private async setState(next: IGameState) {

		this.state = next;

		if (this.state.enter)
			await this.state.enter();
	}
}