import { log } from "cc";
import { IGravityResolver, IGrid } from "db://assets/scripts/game/domain/Contract";


export interface DropMove {

	id: string;
	fromX: number;
	fromY: number;
	toY: number;
}

export class GravityResolver implements IGravityResolver {

	applyGravity(grid: IGrid): DropMove[] {

		const moves: DropMove[] = [];

		for (let x = 0; x < grid.width; x++) {

			let writeY = 0;

			for (let readY = 0; readY < grid.height; readY++) {

				const fromCell = grid.getCell(x, readY);

				if (!fromCell.piece)
					continue;

				if (readY !== writeY) {

					const toCell = grid.getCell(x, writeY);

					moves.push({
						id: fromCell.piece.id,
						fromX: x,
						fromY: readY,
						toY: writeY,
					});

					toCell.piece = fromCell.piece;
					fromCell.piece = null;
				}
				writeY++;
			}
		}

		return moves;
	}
}