import { IGrid, IIdFactory, IRefillService, IRng } from "db://assets/scripts/game/domain/Contract";
import { Grid } from "db://assets/scripts/game/domain/Grid";
import { DropMove } from "db://assets/scripts/game/domain/service/GravityResolver";
import { BlastGroupConfig } from "db://assets/scripts/shared/castledb/storage/sheets/Blast";

export type SpawnMove = DropMove;

export class RefillService implements IRefillService {

	refill(
		grid: Grid,
		rng: IRng,
		idFactory: IIdFactory
	): SpawnMove[] {

		const result = [];
		const additivesCount = [];

		for (let y = 0; y < grid.height; y++) {

			for (let x = 0; x < grid.width; x++) {

				const cell = grid.getCell(x, y);

				if (cell.piece)
					continue;

				cell.piece = { id: idFactory.newId(), type: rng.next() };

				if (!additivesCount[x])
					additivesCount[x] = 0;

				result.push({
					id: cell.piece.id,
					fromX: x,
					fromY: additivesCount[x] + grid.height,
					toY: y
				});

				additivesCount[x]++;
			}
		}

		return result;
	}
}