import { BaseCdbSheet, RawSheetData } from "db://assets/scripts/shared/castledb/BaseCdbSheet";
import { SheetsClassMap, SheetConstructors } from "db://assets/scripts/shared/castledb/storage/Typings";

export interface CdbData extends SheetsClassMap { }

export class CdbData {

    private sheets: Partial<SheetsClassMap> = {};

    constructor(rawDb: Record<string, RawSheetData<any>>) {

        for (const name in SheetConstructors) {
            const ctor = SheetConstructors[name];
            if (!rawDb[name])
                throw new Error(`pre-defined sheet ${name} is not found in cdb!`);
            const sheet = new ctor(rawDb[name], this.sheets);
            // @ts-ignore
            this[name] = this.sheets[name] = sheet;
        }

        for (const name in rawDb) {
            if (name.includes("@") && !this.sheets[name]) {
                const sheet = new BaseCdbSheet<any>(rawDb[name], this.sheets);
                // @ts-ignore
                this[name] = this.sheets[name] = sheet;
            }
        }

    }
}