import { _decorator, Component, Label } from 'cc';

const { ccclass, property, requireComponent } = _decorator;


@ccclass("StatsView")
export class StatsView extends Component {

	private static VICTORY_LOCALE = "!!!";
	private static DEFEAT_LOCALE = " :(";

	@property(Label)
	private turnsCounter: Label;

	@property(Label)
	private scoreCounter: Label;

	@property(Label)
	private gameEnd: Label;

	public showDefeat(): Promise<void> {
		this.gameEnd.node.active = true;
		this.gameEnd.string = StatsView.DEFEAT_LOCALE;
		return Promise.resolve();
	}

	public showVictory(): Promise<void> {
		this.gameEnd.node.active = true;
		this.gameEnd.string = StatsView.VICTORY_LOCALE;
		return Promise.resolve();
	}

	public updateScore(value: number, target: number) {

		return this.scoreCounter.string = `${value} / ${target}`;
	}

	public updateTurns(value: number) {

		this.turnsCounter.string = `${value}`;
	}


}